<template>
    <div class="">

        <!-- image of the day -->
        <div class="card shadow " style="width: 100%;">
            <img v-if="iUrl != ''" v-cloak class="mainImage card-img-top" :src="iUrl" alt="Nasa image of the day"
                title="Nasa image of the day  ">
            <div class="card-body">
                <h4 class="card-title fw-bold text-center">Image of the Day </h4>
                <h6 class="card-title fw-bold pt-2 text-center">{{ ititle }}</h6>
                <hr class="w-50 mx-auto">

                <div v-if="loadingMessage">
                    <div class="progress" role="progressbar" aria-label="Animated striped example" aria-valuenow="75"
                        aria-valuemin="0" aria-valuemax="100">
                        <div class="progress-bar progress-bar-striped progress-bar-animated" style="width: 75%">Please wait,
                            data is loading....</div>
                    </div>
                </div>
                <!-- <p class="card-text ">{{ description.substring(0, 200) }}...</p> -->

                <div class="card-text " v-if="truncDescriptionBox">
                    <p>{{ truncatedDescription }}</p>
                    <div class="text-end">
                        <button v-if="this.description.length > 200" class="btn btn-success btn-sm "
                            @click="fullDescription">Show Details</button>
                    </div>

                </div>

                <div v-if="fullDescriptionBox" class="card-text ">
                    <p>{{ description }}</p>
                    <div class="text-end">
                        <button class="btn btn-success btn-sm" @click="truncDescription">Hide Details</button>
                    </div>

                </div>


            </div>
        </div>



    </div>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            nasaApiKey: '8XRchnUc1jDJrb4hcQJeK4Y4JcDpymuhiZF0DjBD',
            loadingMessage: false,
            description: '',
            iUrl: '',
            ititle: '',
            truncDescriptionBox: true,
            fullDescriptionBox: false,

        }
    },
    computed: {
        truncatedDescription() {
            return this.description.length < 200 ? this.description : this.description.substring(0, 200) + " ..."
        }

    },
    created() {
        this.imageOfTheDay();
    },
    methods: {
        async imageOfTheDay() {
            this.loadingMessage = true;
            const imageOfTheDayKey = `https://api.nasa.gov/planetary/apod?api_key=${this.nasaApiKey}`;
            await axios.get(imageOfTheDayKey)
                .then((response) => {
                    // handle success
                    //console.log(response.data);
                    this.idate = response.data.date;
                    this.description = response.data.explanation;
                    this.iUrl = response.data.hdurl;
                    this.ititle = response.data.title;
                    this.loadingMessage = false;
                })
                .catch((error) => {
                    // handle error
                    console.log(error);
                })
                .finally(() => {
                    // always executed
                });

        },
        fullDescription() {
            this.truncDescriptionBox = false;
            this.fullDescriptionBox = true;
        },
        truncDescription() {
            this.fullDescriptionBox = false;
            this.truncDescriptionBox = true;
        }
    }
}
</script>

<style scoped>
[v-cloak] {
    opacity: 0.5;
}

.mainImage {
    border: 5px solid #000;
}
</style>