<template>
    <div class="" style="width: 100%;">

        <!-- image of the day -->
        <div class="card shadow " >

            <div class="card-body">
                <h4 class="card-title fw-bold text-center">Near Earth astroids </h4>
                <p class="text-center" v-if="astroids.length>0"><span class="badge text-bg-primary rounded-pill">{{astroidsLength}}</span> astroids found<br><span class="fw-bold">{{ closestAstroid() }}</span> : closest asteroid</p>
                <hr class="w-50 mx-auto">
                <div v-if="loadingMessage">
                    <div class="progress" role="progressbar" aria-label="Animated striped example" aria-valuenow="55"
                        aria-valuemin="0" aria-valuemax="100">
                        <div class="progress-bar progress-bar-striped progress-bar-animated" style="width: 55%">Please wait, data is loading.... {{ loadingProgress }}%</div>
                    </div>
                </div>
                <table  v-else v-cloak  class="table striped  align-left">
                    <thead >
                        <tr>
                            <th>No.</th>
                            <th>Astroids Name</th>
                            <th>Close Approach Data</th>
                            <th>Distance</th>
                        </tr>
                    </thead>
                    <tbody >
                        
                        <tr v-for="(a, index) in astroids" :key="a.id">
                            <td>{{ index + 1 }}</td>
                            <td>{{ a.name }}</td>
                            <td>{{ getCloseApproachData(a) }}</td>
                            <td>
                                <ul v-if="a.close_approach_data.length >0" class="listStyleNone">
                                    <li v-for="(key, value) in a.close_approach_data[0].miss_distance" :key="value">
                                        {{ value }} : {{ key }}
                                    </li>
                                </ul>
                            </td>
                        </tr>
                    </tbody>
                </table>

            </div>
        </div>



    </div>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            nasaApiKey: '8XRchnUc1jDJrb4hcQJeK4Y4JcDpymuhiZF0DjBD',
            astroids: [],
            loadingMessage: false,
            loadingProgress: 0

        }
    },
    computed : {
        astroidsLength(){
            return this.astroids.length;
        }
    },
    created() {
        this.fetchAstroids();
    },
    methods: {
        async fetchAstroids() {
            this.loadingMessage = true;
            const nearEarthObjectKey = `https://api.nasa.gov/neo/rest/v1/neo/browse?api_key=${this.nasaApiKey}`;
            await axios.get(nearEarthObjectKey)
                .then((response) => {
                    // handle success
                    this.astroids = response.data.near_earth_objects; //.slice(0, 10)
                    this.loadingMessage = false;
                })
                .catch((error) => {
                    // handle error
                    console.log(error);
                })
                .finally(() => {
                    // always executed
                });
        },
        getCloseApproachData(a) {
            if (a.close_approach_data.length > 0) {
                return a.close_approach_data[0].close_approach_date_full;
            }
            else {
                return 'n/a'
            }
        },
        closestAstroid(){
            let closeNeo = this.astroids.filter((n)=>{
                return n.close_approach_data.length > 0
            })
            .map((n)=>{
                return {name : n.name, miles: n.close_approach_data[0].miss_distance.miles}
            });
            let sortedNeo = closeNeo.sort((a,b)=>{
                return a.miles - b.miles
            });
            return sortedNeo[0].name;
        }
    }
}
</script>

<style scoped>
[v-cloak] {
    opacity: 0.5;
}

.mainImage {
    border: 5px solid #000;
}
.listStyleNone{ list-style: none;}
</style>